  /*滚动条里面可以拖动的那部分（位置5）*/
  background: #f7f8f8;
  border-radius: 10px; }

.sc-textarea::-webkit-scrollbar-corner {
  /*边角（位置6）*/ }

.sc-textarea::-webkit-scrollbar-resizer {
